
import React, { useEffect, useState } from "react";
import Header from "./../Header";


// import { BiChevronsRight } from "react-icons/bs";
import { BiChevronsRight } from "react-icons/bi";

// import { FaFacebookF } from "react-icons/fi";
// import { FaFacebookF } from "react-icons/fc";
import {
  FaFacebookF,
  FaLinkedinIn,
  FaTwitter,
  FaPhoneAlt,
} from "react-icons/fa";


import './team.css';




import AboutImg from "./img/about-title-img.jpg";

import point1 from "./img/about-icon-1.png";
import point2 from "./img/about-icon-2.png";
import point3 from "./img/about-icon-3.png";
import sign from "./img/h1-img-6.png";


import { css } from "@emotion/react";
import ClipLoader from "react-spinners/FadeLoader";

import { AiOutlineTwitter } from "react-icons/ai";

import API from "./../common/url";
import axios from "axios";

const Team = () => {



//   const [ state, setstate] = useState(false);
  const [articleList, setArticleList] = useState(false);




  useEffect(() => {
    axios.get(API + "/api/gets_article").then((res) => {
      console.log("res.data", res.data);

      setArticleList(res.data);
    });
  }, []);





const loadercss = {
  height: "300px",
  textAlign: "center",
};
const loadicon = {
  position: "relative",
  top: "86px !important",
};




    return (
      <div id="about4">
        <Header />
        <div class="eltdf-content">
          <div class="eltdf-content-inner">
            <div
              class="eltdf-title-holder eltdf-centered-type eltdf-title-full-width eltdf-title-va-header-bottom eltdf-preload-background eltdf-has-bg-image eltdf-bg-parallax"
              style={{
                height: "460px",
                backgroundColor: "#29191a",
                backgroundImage: `url(${AboutImg})`,
              }}
              data-height="460"
            >
              <div class="eltdf-title-image">
                <img itemprop="image" src={AboutImg} alt="i" />
              </div>
              <div class="eltdf-title-wrapper" style={{ height: "460px" }}>
                <div class="eltdf-title-inner">
                  <div class="eltdf-grid">
                    <h1
                      class="eltdf-page-title entry-title"
                      style={{ color: "#ffffff" }}
                    >
                      Our Team Members
                    </h1>
                  </div>
                </div>
              </div>
            </div>

            <div class="eltdf-row-grid-section-wrapper ">
              <div class="eltdf-row-grid-section setwwidthWeb">
                {/* {articleList ? (
                    <div>
                         {
                             articleList.map((item,index) => {
                  return (
                  );
                })}
                </div>) : null } */}
                <br />
                <div class="vc_row wpb_row vc_inner vc_row-fluid vc_column-gap-20">
                  {articleList ? (
                    <div>
                      {articleList.map((item, index) => {
                        return (
                          <div
                            key={index}
                            class="wpb_column vc_column_container vc_col-sm-6 vc_col-lg-3"
                          >
                            <div class="vc_column-inner">
                              <div class="wpb_wrapper">
                                <div class="eltdf-team-holder">
                                  <div class="eltdf-team-inner">
                                    <div class="eltdf-team-image">
                                      <img
                                        style={{
                                          height: "300px",
                                          width: "100%",
                                        }}
                                        src={API + "/api/uploads/" + item.image}
                                        class="attachment-full size-full"
                                        alt="a"
                                        srcset={
                                          API + "/api/uploads/" + item.image
                                        }
                                        sizes="(max-width: 800px) 100vw, 800px"
                                      />
                                    </div>
                                    <div class="eltdf-team-info">
                                      <h5
                                        class="eltdf-team-name"
                                        style={{ height: "45px" }}
                                      >
                                        {item.title}
                                      </h5>
                                      <div class="eltdf-team-position">
                                        {item.position}
                                      </div>
                                      <p
                                        class="eltdf-team-text"
                                        dangerouslySetInnerHTML={{
                                          __html:
                                            item.description &&
                                            item.description,
                                        }}
                                      ></p>
                                      <div
                                        class="eltdf-team-social-holder"
                                        style={{ display: "none" }}
                                      >
                                        <span class="eltdf-team-icon">
                                          {" "}
                                          <span class="eltdf-icon-shortcode eltdf-normal   ">
                                            <a
                                              class=""
                                              // href=""https://www.facebook.com/QodeInteractive/"
                                              target="_blank"
                                            >
                                              <FaFacebookF />
                                            </a>
                                          </span>
                                        </span>
                                        <span class="eltdf-team-icon">
                                          {" "}
                                          <span class="eltdf-icon-shortcode eltdf-normal   ">
                                            <a
                                              class=""
                                              // href=""https://twitter.com/QodeInteractive"
                                              target="_blank"
                                            >
                                              <FaTwitter />
                                            </a>
                                          </span>
                                        </span>
                                        <span class="eltdf-team-icon">
                                          {" "}
                                          <span class="eltdf-icon-shortcode eltdf-normal   ">
                                            <a
                                              class=""
                                              // href=""https://www.linkedin.com/company/qode-themes/"
                                              target="_blank"
                                            >
                                              <FaLinkedinIn />
                                            </a>
                                          </span>
                                        </span>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                                <div
                                  class="vc_empty_space"
                                  style={{ height: "46px" }}
                                >
                                  <span class="vc_empty_space_inner"></span>
                                </div>
                              </div>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  ) : (
                    <div className="sweet-loading" style={loadercss}>
                      <ClipLoader size={15} style={loadicon} />
                    </div>
                  )}
                </div>
              </div>
              <br /> <br />
            </div>
          </div>
        </div>
      </div>
    );
}

export default Team
