 const onChangeHandler = (e) => {
   if (e.target.name == "image") {
     let val = e.target.files[0];
     setstate({ ...state, [e.target.name]: val });
   } else {
     setstate({ ...state, [e.target.name]: e.target.value });
   }
 };


