import React, { Component } from "react";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";

import img1 from './imgs/h1-testimonial-2.6b6ac840.png';

import './test.css';


export default class Testimonials extends Component {
  render() {
    return (
      <Carousel
        showArrows={true}
        infiniteLoop={true}
        showThumbs={false}
        showStatus={false}
        autoPlay={true}
        interval={6100}
      >
        <div>
          <img
            src={img1}
            style={{ height: "100px", width: "109px", borderRadius: "50%" }}
          />
          <div className="myCarousel">
            <h3>Shirley Fultz</h3>
            <h4>Designer</h4>
            <p>
              Aliquam lorem ante, dapibus in, viverra quis, feugiat a, tellus.
              Phasellus viverra nulla ut metus varius laoreet. Quisque rutrum.
              Aenean imperdiet.
            </p>
          </div>
        </div>

        <div>
          <img
            src={img1}
            style={{ height: "100px", width: "109px", borderRadius: "50%" }}
          />
          <div className="myCarousel">
            <h3>Daniel Keystone</h3>
            <h4>Designer</h4>
            <p>
              Sagittis vitae et leo duis. Lacus suspendisse faucibus interdum
              posuere lorem ipsum dolor sit. Arcu cursus vitae congue mauris
              rhoncus aenean vel elit.
            </p>
          </div>
        </div>

        <div>
          <img
            src={img1}
            style={{ height: "100px", width: "109px", borderRadius: "50%" }}
          />
          <div className="myCarousel">
            <h3>Theo Sorel</h3>
            <h4>Designer</h4>
            <p>
              Aliquam lorem ante, dapibus in, viverra quis, feugiat a, tellus.
              Phasellus viverra nulla ut metus varius laoreet. Quisque rutrum.
              Aenean imperdiet.
            </p>
          </div>
        </div>
      </Carousel>
    );
  }
}
