import React from 'react';

// Images
import slider1 from './landingPage/Images/slider1.jpeg';
import slide2 from "./landingPage/Images/slider2.jpeg";
import slide3 from "./landingPage/Images/slider3.jpeg";
import slide4 from "./landingPage/Images/slider4.jpeg";

// Components
import HeroSlider, {
  Slide,
  ButtonsNav,
  Nav,
  OverlayContainer,
} from 'hero-slider';
import Wrapper from './UI/Wrapper/Wrapper';
import Title from './UI/Title/Title';
import Subtitle from './UI/Subtitle/Subtitle';

const ZoomSlider = () => {
  const nextSlideHandler = React.useRef()
  const previousSlideHandler = React.useRef()

  return (
    <HeroSlider
      nextSlide={nextSlideHandler}
      previousSlide={previousSlideHandler}
      orientation="horizontal"
      initialSlide={1}
      onBeforeChange={(previousSlide, nextSlide) =>
        console.log("onBeforeChange", previousSlide, nextSlide)
      }
      onChange={(nextSlide) => console.log("onChange", nextSlide)}
      onAfterChange={(nextSlide) => console.log("onAfterChange", nextSlide)}
      style={{
        backgroundColor: "#000",
      }}
      settings={{
        // slidingDuration: 500,
        // slidingDelay: 200,
        // sliderColor: "inherit",
        // sliderFadeInDuration: 100,
        // navbarFadeInDuration: 1000,
        // navbarFadeInDelay: 500,
        // isSmartSliding: true,
        // shouldDisplayButtons: true,
        // shouldAutoplay: true,
        // shouldSlideOnArrowKeypress: false,
        // autoplayDuration: 8000,
        // autoplayHandlerTimeout: 1000,
        // width: "100%",
        // height: "100%",

        // backgroundPosition: "top center",

        slidingDuration: 500,
        slidingDelay: 100,
        shouldAutoplay: true,
        shouldDisplayButtons: false,
        autoplayDuration: 5000,
        height: "100vh",
      }}
    >
      <OverlayContainer>
        <Wrapper>
          <Title style={{ color: "white" }}>
            {/* Minorities Alliance Pakistan */}
          </Title>
          <h2 style={{ color: "white", fontSize: "18px" }}>
            {/* MAP believes in human dignity and values.We speak for speechless. We
            stand with persecuted, marginalized and faith victims. */}
          </h2>
        </Wrapper>
      </OverlayContainer>

      <Slide
        navDescription="slider1 - Austria"
        background={{
          backgroundImage: slider1,
          backgroundAnimation: "zoom",
        }}
      />

      <Slide
        navDescription="slide2 - Iceland"
        background={{
          backgroundImage: slide2,
          backgroundAnimation: "zoom",
        }}
      />

      <Slide
        navDescription="slide3 - USA"
        background={{
          backgroundImage: slide3,
          backgroundAnimation: "zoom",
        }}
      />

      <Slide
        navDescription="Moraine Lake - Canada"
        background={{
          backgroundImage: slide4,
          backgroundAnimation: "zoom",
        }}
      />

      <ButtonsNav
        isNullAfterThreshold
        position={{
          top: 0,
          left: "50%",
          transform: "translateX(-50%)",
        }}
      />
      <Nav />
    </HeroSlider>
  );
}

export default ZoomSlider;