import React, { Component } from "react";


import img1 from './imgs/h1-testimonial-2.6b6ac840.png';

import car1 from './imgs/h1-slider-1-slide-4.jpg';
import car2 from "./imgs/h1-slider-img-2.jpg";

import car3 from "./imgs/h1-slider-img-3.jpg";


import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";

export default class Carousal extends Component {
  render() {
    return (
      <Carousel showArrows={true}>
        <div>
          <img
            src={car1}
            style={{ width: "100% !important", borderRadius: "none" }}
          />
          <p className="legend">Legend 1</p>
        </div>
        <div>
          <img
            src={car2}
            style={{ width: "100% !important", borderRadius: "none" }}
          />
          <p className="legend">Legend 2</p>
        </div>
        <div>
          <img
            src={car3}
            style={{ width: "100% !important", borderRadius: "none" }}
          />
          <p className="legend">Legend 3</p>
        </div>
        <div>
          <img
            src={car1}
            style={{ width: "100% !important", borderRadius: "none" }}
          />
          <p className="legend">Legend 4</p>
        </div>
      </Carousel>
    );
  }
}
