import React from 'react'
import LandingPage from './landingPage/LandingPage';

import ZoomSlider from "./Slider";

import { BrowserRouter, Switch, Route, Link } from "react-router-dom";
import { Provider } from "react-redux";
import store from "./../redux/store";
import Header from "./../Components/Header";
import Footer from "./../Components/Footer";

import AboutUs from "./AboutUs/aboutus";
import Gallary from "./Gallary/gallarypage";
import Blogpage from './BlogPage/blogpage';
// import Mapblog from './MapBlog/mapblog';

import Eventsday from './Events/Eventsday';
import EvntsList from './Events/Eventslists';

import Singleevent from './Events/Singleevent';
import GetInTouch from './Contact/GetInTouch';
import Donation from './Donation/donation';
import Gallarypagebackend from './Gallary/gallarypagebackend';
import GallaryVedio from './GallaryVideo/gallarypage';

import EventHistory from './Events/HistoryEvents';

import Mapblog from './mapBlog/mapblog';

import Team from './TeamMembers/team';

import Testimonials from './Testimonial/testimonial';

import Carousal from './CarousaDonate/carousal';

import BlogpageAll from './BlogPage/blogPageAll';

const MapMinistry =() =>{
    return (
      <Provider store={store}>
        <div>
          <BrowserRouter>
            {/* <Header /> */}
            <Switch>
              <Route exact path="/" component={LandingPage} />
              <Route path="/AboutUs" component={AboutUs} />
              <Route path="/Gallary" component={Gallary} />
              <Route path="/Blogpage/:id" component={Blogpage} />
              <Route path="/Eventsday" component={Eventsday} />
              <Route path="/EventHistory" component={EventHistory} />
              <Route path="/EvntsList" component={EvntsList} />
              <Route path="/Singleevent/:id" component={Singleevent} />
              <Route path="/GetInTouch" component={GetInTouch} />
              <Route path="/Donation" component={Donation} />
              <Route path="/gallaryyback" component={Gallarypagebackend} />
              <Route path="/Team" component={Team} />
              <Route path="/GallaryVedio" component={GallaryVedio} />
              <Route path="/Testimonials" component={Testimonials} />
              <Route path="/Carousal" component={Carousal} />
              <Route path="/BlogpageAll" component={BlogpageAll} />

              {/* <Route path="/Mapblog" component={Mapblog} /> */}
              {/* <Route path="/Footer" component={Footer} /> */}
            </Switch>
            <br />

            <br />
            <Footer />
          </BrowserRouter>
        </div>
      </Provider>
    );
}

export default MapMinistry
