import React, { useEffect, useState } from "react";

import "./event.css";

import Header from "./../Header";
import axios from "axios";

import API from "./../common/url";

import { MdDateRange, MdLocationOn, MdViewAgenda } from "react-icons/md";
import { BrowserRouter, Route, Switch, Link } from "react-router-dom";

import { AiOutlineFieldTime } from "react-icons/ai";


import { FcExpired } from "react-icons/fc";

import { IoPersonCircleOutline } from "react-icons/io5";

const HistoryEvents = () => {
  const [state, setstate] = useState(false);
  const [state2, setstate2] = useState(false); // Use this to add filter on text change

  const [respLength, setrespLength] = useState(''); // Use this to add filter on text change

  useEffect(() => {
       window.scrollTo(0, 0);
    axios.get(API + "/api/award/get").then((res) => {
      console.log("res.data", res.data);

      var today = new Date();
      var isoformatToday = today.toISOString();
      console.log("today", isoformatToday);
      var resp = res.data;
      let result = resp.filter((item) => item.date < isoformatToday);
      console.log("parameter", result);

      setstate(result);
      setstate2(result);

  console.log("resultLength", result.length);
  var leng = result.length;
      setrespLength(leng);

    });
  }, []);

  const resetall = (e) => {
    e.preventDefault();

    let result = state2.filter((item) => item);
    setstate(result);
  };

  const searchTextField = (e) => {
    e.preventDefault();
    let searching = e.target.value;
    let result = state2.filter(
      (item) =>
        item.title && item.title.toLowerCase().includes(searching.toLowerCase())
    );
    setstate(result);
  };
  const searchDatePick = (e) => {
    e.preventDefault();
    let searching = e.target.value;

    console.log("date", searching);

    let result = state2.filter(
      (item) =>
        item.date && item.date.toLowerCase().includes(searching.toLowerCase())
    );
    setstate(result);
  };
  return (
    <div id="eventday4">
      <Header />

      <div style={{ marginTop: "115px" }}>
        <br />
        <h4 style={{ textAlign: "left", paddingLeft: "52px" }}>
          Events For 14.10.2021
        </h4>

        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-around",
          }}
        >
          <div style={{ width: "30%" }}>
            <h6>DAY OF</h6>
            <br />
            <input
              type="date"
              onChange={searchDatePick}
              style={{
                width: "87%",
                height: "42px",
                background: "antiquewhite",
                border: "none",
                borderRadius: "3px",
                padding: "10px",
              }}
              format="yyyy-MM-dd"
            />
            {/* var date = $('#datepicker').datepicker({ dateFormat: 'dd-mm-yy' }).val(); */}
          </div>
          <div style={{ width: "30%" }}>
            <h6>SEARCH</h6>
            <br />
            <input
              type="search"
              placeholder="Keyword"
              onChange={searchTextField}
              style={{
                width: "87%",
                height: "60px",
                background: "antiquewhite",
                border: "none",
                borderRadius: "3px",
                padding: "10px",
              }}
            />
          </div>
          <div style={{ width: "30%", textAlign: "initial" }}>
            <h6>&nbsp;</h6>
            <br />
            <input
              type="button"
              value="Reset"
              onClick={resetall}
              style={{
                width: "40%",
                height: "60px",
                background: "black",
                color: "white",
                border: "none",
                borderRadius: "3px",
                padding: "10px",
              }}
            />
          </div>
        </div>
      </div>

      <br />
      <h5>View As:</h5>
      <hr style={{ width: "90%", height: "100%" }} />

      {respLength > 0 ? (
        <div>
          {state ? (
            <div>
              {state.map((item, index) => {
                var htmlString = item.description;
                var plainString = htmlString.replace(/<[^>]+>/g, "");

                console.log("kjbjk", plainString);
                {
                  /* console.log(plainString.slice(0, 100)); */
                }

                return (
                  <div
                    key={index}
                    style={{
                      marginBottom: "20px",
                      display: "flex",
                      flexDirection: "row",
                      justifyContent: "space-evenly",
                    }}
                  >
                    <div style={{ width: "35%" }}>
                      <img
                        style={{ width: "100%" }}
                        src={API + "/api/uploads/" + item.image}
                      />
                    </div>
                    <div style={{ width: "40%" }}>
                      <h4>{item.title}</h4>
                      <div>
                        <MdDateRange />
                        {item.date}
                      </div>
                      <div>
                        {" "}
                        <AiOutlineFieldTime /> {item.time}
                      </div>
                      <div>
                        {" "}
                        <IoPersonCircleOutline /> {item.host}
                      </div>
                      <div>
                        {" "}
                        <MdLocationOn /> {item.place}
                      </div>

                      <div>
                        {/* <MdViewAgenda /> */}
                        <p>{plainString.slice(0, 200)}</p>
                      </div>
                      <Link to={`/Singleevent/${item._id}`}>Read More</Link>
                    </div>
                  </div>
                );
              })}
            </div>
          ) : (
            <div>Loading...</div>
          )}
        </div>
      ) : (
        <div>
          {" "}
          <FcExpired size="23" />
          No History Available Right Now , Please Go to
          <Link to="/Eventsday" style={{ textDecoration: "underline" }}>
            {" "}
            Upcomming History
          </Link>
        </div>
      )}
    </div>
  );
};;

export default HistoryEvents;
